<template lang="pug">
  .v-fixed
    .v-fixed-space(:style="style")
    .v-fixed-con(:style="`left: '260px'}`" ref="fixedCon")
      slot
</template>
<script>
export default {
  name: 'fixed',
  data() {
    return {
      height: 80
    };
  },
  computed: {
    style() {
      return {
        height: this.height + 'px'
      };
    }
  },
  mounted() {
    // console.log('进组件fied了');
    this.height = this.$refs.fixedCon.offsetHeight;
  }
};
</script>
<style lang="scss" scoped>
  .v-fixed {
    position: relative;
    z-index: 100;
    .v-fixed-space{
      display: block;
      content: " ";
      width:100% ;
      position: relative;
    }
    .v-fixed-con {
      box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
      border-top: 1px solid #e8eaec;
      background: #fff;
      position: fixed;
      right: 0;
      bottom: 0;
      padding: 20px;
      width:100% ;
    }
  }
</style>
