<template>
  <div class="easy-table-container">
    <div class="table-tools" v-if="tools">
      <slot></slot>
    </div>
    <div class="table-wrapper">
      <el-table
        ref="table"
        v-loading="tableLoading"
        :highlight-current-row="highlightCurrentRow"
        :data="pageTableData"
        :height="tableHeight"
        :max-height="maxHeight"
        :border="border"
        :show-summary="showSummary"
        :sum-text="sumText"
        :summary-method="getSummaries"
        :need-summary-index="needSummaryIndex"
        :cell-style="cellStyle"
        empty-text="暂无数据"
        @row-click="rowClickHandler"
        @current-change="currentChangeHandler"
        @select="selectHandler"
        @select-all="selectAllHandler"
        :header-cell-style="headerStyle"
        :stripe="stripe"
        @row-dblclick="rowDblclick"
      >
        <el-table-column v-if="selection" type="selection" width="60" />
        <el-table-column
          v-if="showIndex"
          type="index"
          :label="indexOptions.label || '#'"
          :width="indexOptions.width || 50"
          :align="indexOptions.align"
          :fixed="indexOptions.fixed"
        />
        <el-table-column
          v-for="(column, index) in showOfColumns"
          :sortable="column.sortable || false"
          :key="column.name"
          :prop="column.name"
          :label="column.title"
          :fixed="column.fixed"
          :width="column.width"
          :min-width="column.minWidth"
          :show-overflow-tooltip="column.showOverflowTooltip"
          :align="column.align || align"
          :header-align="column.headerAlign || headerAlign"
        >
          <template slot-scope="scope">
            <template v-if="column.type==='label'">
              <el-tag
                v-for="label in column.labels"
                :key="label.name"
                v-show="label.show ? label.show(scope.row) : true"
                :type="label.tag(scope.row, scope.column)"
                size="mini"
              >
                {{label.name || format(index, scope.row, scope.column)}}
              </el-tag>
              <span v-if="!column.buttons">
                {{format(index, scope.row, scope.column)}}
              </span>
              <el-button
                v-else
                v-for="button in column.buttons"
                :key="button.name"
                v-show="button.show ? button.show(scope.row) : true"
                @click="button.click(scope.$index, scope.row)"
                type="text"
                :size="button.size ? button.size : 'small'"
              >
                {{button.name || format(index, scope.row, scope.column)}}
              </el-button>
            </template>
            <template v-else-if="column.type==='operate'">
              <!-- v-for和v-if混用eslint会报错, 此处不做处理, 如改用v-show, button节点索引不准确, 会导致对不齐等样式问题 -->
              <el-button
                v-for="button in column.buttons"
                :key="button.name"
                v-if="button.show ? button.show(scope.row) : true"
                @click="button.click(scope.$index, scope.row)"
                type="text"
                :size="button.size ? button.size : 'small'"
              >
                {{button.name || format(index, scope.row, scope.column)}}
              </el-button>
            </template>
            <template v-else-if="column.tag || column.tagColor">
              <el-tag v-if="column.tag" :type="column.tag(scope.row, scope.column)" size="mini">
                {{format(index, scope.row, scope.column)}}
              </el-tag>
              <el-tag v-else :color="column.tagColor(scope.row, scope.column)" size="mini">
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              </el-tag>
            </template>
            <span v-else :style="{width: column.width}" :title="format(index, scope.row, scope.column)">
              {{format(index, scope.row, scope.column)}}
            </span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div style="display: flex; justify-content: center">
      <div v-if="pageTotal">
        <el-pagination layout="total" :total="pageTotal"></el-pagination>
      </div>
      <div>
        <el-pagination
                v-show="data.length > pageSize ? true : showPagination"
                background
                :layout="pageTotal ? 'sizes, prev, pager, next, slot' : 'total, sizes, prev, pager, next, slot'"
                :total="data.length"
                @current-change="currentPageChangeHandler"
                @size-change="sizeChangeHandler"
                :current-page.sync="currentPage"
                :page-sizes="[5, 10, 20, 30, 40, 50, 100]"
                :page-size="pageSize"
                :disabled="loading"
        >
          <template v-slot>
            <el-button
                    @click="loadMore"
                    :disabled="!hasMore || loading"
                    v-show="showLoadMore"
                    :loading="loading"
            >
              {{loadMoreText}}
            </el-button>
          </template>
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import {
  Button, Table, TableColumn, Tag, Pagination,
} from 'element-ui';

export default {
  name: 'easy-table',
  props: {
    headerStyle: {
      type: [Function, Object],
    },
    selectedRow: {
      type: Number,
      default: 1,
    },
    pageTotal: {
      type: Number,
      default: 0,
    },
    pageSize: {
      type: Number,
      default: 10,
    },
    showPagination: {
      type: Boolean,
      default: false,
    },
    border: {
      type: Boolean,
      default: false,
    },
    tableLoading: {
      type: Boolean,
      default: false,
    },
    highlightCurrentRow: {
      type: Boolean,
      default: true,
    },
    showLoadMore: {
      type: Boolean,
      default: false,
    },
    showIndex: Boolean,
    selection: {
      type: Boolean,
      default: false,
    },
    tableHeight: {
      type: [Number, String],
      // default: 585,
    },
    maxHeight: {
      type: [Number, String],
      // default: 585,
    },
    columns: Array,
    data: Array,
    tools: {
      type: Boolean,
      default: true,
    },
    showSummary: {
      type: Boolean,
      default: false,
    },
    sumText: {
      type: String,
      default: '合计',
    },
    align: {
      type: String,
      default: 'left',
    },
    headerAlign: {
      type: String,
      default: 'left',
    },
    primaryKey: {
      type: Array,
      default() {
        return [];
      },
    },
    needSummaryIndex: {
      type: Array,
      default() {
        return [];
      },
    },
    cellStyle: {
      type: [Function, Object],
    },
    resetCurrentPage: {
      type: Boolean,
      default: false,
    },
    stripe:{
      type: Boolean,
      default: true,
    },
    indexOptions: {
      type: Object,
      default: () => {
        return {}
      },
    }
  },
  components: {
    'el-button': Button,
    'el-table': Table,
    'el-table-column': TableColumn,
    'el-tag': Tag,
    'el-pagination': Pagination,
  },
  data() {
    return {
      loading: false,
      hasMore: true,
      currentPage: 1,
      // pageSize: 10,
      allSelections: [],
    };
  },
  computed: {
    pageTableData() {
      const results = [];
      let i = (this.currentPage - 1) * this.pageSize;
      let pageLimit = this.data.length;
      if (this.currentPage * this.pageSize > this.data.length) {
        pageLimit = this.data.length;
      } else {
        pageLimit = this.currentPage * this.pageSize;
      }
      for (i; i < pageLimit; i++) {
        results.push(this.data[i]);
      }
      return results;
    },
    pages() {
      return Math.ceil(this.data.length / this.pageSize);
    },
    loadMoreText() {
      if (this.hasMore) {
        return '更多';
      }
      return '没有更多';
    },
    showOfColumns() {
      return this.columns.filter(item => {
        if (!item.hasOwnProperty('show')) {
          return true
        }
        if (typeof item.show == 'function') {
          return item.show()
        } else {
          return item.show
        }
      })
    },
  },
  methods: {
    // 最后一行，按指定列做合计
    getSummaries (param) {
      const { columns, data } = param;
      const sums = [];
      let sumText = this.sumText;
      let needSummaryIndex = this.needSummaryIndex;
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = sumText;
          return;
        }
        if (needSummaryIndex.includes(index)) {
          const values = data.map(item => Number(item[column.property]));
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            // sums[index] += ' 元';
          } else {
            sums[index] = '';
          }
        } else {
          sums[index] = '';
        }
      });
      return sums;
    },
    clearSelection () {
      this.$refs.table.clearSelection();
    },
    currentChangeHandler (currentRow, oldCurrentRow) {
      this.$emit('current-change', currentRow, oldCurrentRow);
    },
    currentPageChangeHandler (currentPage) {
      if (currentPage === this.pages) {
        if (currentPage === 1) {
          this.currentPage = 1;
          return;
        }
      }
      this.currentPage = currentPage;
    },
    closeLoading () {
      this.loading = false;
    },
    doLayout () {
      this.$refs.table.doLayout();
    },
    format (index, row, column) {
      if (this.showOfColumns[index].formatter) {
        return this.showOfColumns[index].formatter.call(this, row, column, row[column.property]);
      } else {
        return row[column.property];
      }
    },
    getAllSelections () {
      return this.allSelections;
    },
    loadMore() {
      if (this.data.length >= 200 && this.data.length % 200 === 0) {
        this.hasMore = true;
        if (this.primaryKey.length) {
          let data = {};
          this.primaryKey.forEach(key => {
            data['locate_' + key] = this.data[this.data.length - 1][key];
          });
          this.$emit('load-more', data);
        } else {
          this.$emit('load-more', this.data[this.data.length - 1].row_id);
        }
        this.showLoading();
      } else {
        this.hasMore = false;
      }
    },
    rowClickHandler(row) {
      if (this.primaryKey.length) {
        this.currentRowIndex = this.primaryKey.map(key => {
          return row[key];
        }).join('-');
      } else {
        this.currentRowIndex = row.row_id;
      }
    },
    showLoading () {
      this.loading = true;
    },
    setCurrentRow(row) {
      this.$refs.table.setCurrentRow(row);
    },
    selectHandler (selection, row) {
      this.allSelections = selection;
      this.$emit('select', selection);
    },
    selectAllHandler (selection) {
      this.allSelections = selection;
      this.$emit('select-all', selection);
    },
    sizeChangeHandler (val) {
      this.pageSize = val;
      if (this.currentPage >= this.pages) {
        if (this.currentPage === 1) {
          this.currentPage = 1;
        }
      }
    },
    rowDblclick(row, column, event) {
      this.$emit('row-dblclick', row, column, event);
    },
  },
  watch: {
    data(newValue, oldValue) {
      if (newValue && oldValue && oldValue.length !== newValue.length) {
        this.hasMore = true;
        if (this.currentPage > this.pages) {
          this.currentPage = 1;
        }
      } else if (newValue && oldValue && oldValue.length > newValue.length) {
        this.currentPage = 1;
        this.hasMore = true;
      }
      if (this.resetCurrentPage) {
        this.currentPage = 1;
      }
      this.closeLoading();
      this.$nextTick(() => {
        let row = newValue.filter(item => {
          if (this.primaryKey.length) {
            let primaryKey = this.primaryKey.map(key => {
              return item[key];
            }).join('-');
            return primaryKey === this.currentRowIndex;
          } else {
            return item.row_id === this.currentRowIndex;
          }
        });
        if (row.length) {
          if (this.selectedRow == 1) { // 默认给1选择第一行
            this.$refs.table.setCurrentRow(row[0]); // 默认第一行
          } else {
            this.$refs.table.setCurrentRow(); // 给0不选择默认行
          }
        }
      });
    },
    tableHeight() {
      this.$refs.table.doLayout();
    },
  },
  mounted() {
    // console.log(this.selection);
  }
};
</script>
<style lang="scss" scoped>
  .easy-table-container {
    width: 100%;
  }
  p {
    margin: 10px;
    font-size:24px;
    font-weight: bold;
  }
  .table-tools {
    padding: 8px 8px 4px;
    position: relative;
    .btn-right {
      float: right;
    }
  }
  .table-wrapper {
    padding: 0;
  }
  ::v-deep.el-table {
    font-size: 14px;
    tr {
      height: 48px;
    }
    th {
      padding: 15px 0;
    }
    tr, td {
      font-weight: normal;
    }
    &::before{
      content: none !important;
    }
    .el-table__body-wrapper {
      /*滚动条整体部分*/
      &::-webkit-scrollbar {
        display: block;
        width: 10px;
        height: 10px;
        background-color: transparent;
      }
      /*滚动条里面的小方块*/
      &::-webkit-scrollbar-thumb {
        box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.05);
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.05);
        background-color: rgba(145, 148, 164, 0.15);
        border-radius: 10px;
      }
      /*滚动条里面的轨道*/
      &::-webkit-scrollbar-track {
        border-radius: 10px;
        box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.15);
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.15);
        background-color: #FFFFFF;
      }
    }
    .el-table__row.current-row, .el-table__row--striped.current-row {
      & > td {
        background-color: #c5cfda !important;
      }
    }
  }
  .el-pagination {
    height: 38px;
    padding: 6px 0;
    text-align: center;
  }
</style>
