import Vue from 'vue'
import Router from 'vue-router'
const index = () => import('@/pages/index');
const home = () => import('@/pages/home');
const lostPage = () => import('@/pages/404');

const backgroundComponents = () => import('@/pages/backgroundComponents/tableSearchDemo');
const easyTableDemo = () => import('@/pages/backgroundComponents/easyTableDemo');
const descriptionsTableDemo = () => import('@/pages/backgroundComponents/descriptionsTableDemo');
const rollingDynamicDemo = () => import('@/pages/backgroundComponents/rollingDynamicDemo');
const fixedDemo = () => import('@/pages/backgroundComponents/fixedDemo');
const animationDemo = () => import('@/pages/backgroundComponents/animationDemo');

const gsap = () => import('@/pages/gsap/gsap');

const threeDDemo = () => import('@/pages/3DComponents/threeDDemo');
const newPlayer = () => import('@/pages/other/newPlayer');

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '',
      component: index,
      children: [
        { path: '/', name: 'home', component: home },
      ]
    },
    {
      path: '/404',
      name: '',
      component: lostPage,
    },
    {
      path: '/backgroundComponents',
      name: '后台管理系统组件',
      component: index,
      children: [
        { path: 'tableSearchDemo', name: 'tableSearchDemo', component: backgroundComponents },
        { path: 'easyTableDemo', name: 'easyTableDemo', component: easyTableDemo },
        { path: 'descriptionsTableDemo', name: 'descriptionsTableDemo', component: descriptionsTableDemo },
        { path: 'rollingDynamicDemo', name: 'rollingDynamicDemo', component: rollingDynamicDemo },
        { path: 'fixedDemo', name: 'fixedDemo', component: fixedDemo },
        { path: 'animationDemo', name: 'animationDemo', component: animationDemo },
      ]
    },
    {
      path: '/gsap',
      name: 'gsap动画',
      component: index,
      children: [
        { path: 'gsap', name: 'gsap', component: gsap },
      ]
    },
    {
      path: '/3DComponents',
      name: '3D图',
      component: index,
      children: [
        { path: 'threeDDemo', name: 'threeDDemo', component: threeDDemo },
      ]
    },
    {
      path: '/other',
      name: '其他效果',
      component: index,
      children: [
        { path: 'newPlayer', name: 'newPlayer', component: newPlayer },
      ]
    },
  ]
})
