<template>
  <div class="search-panel">
    <el-form :inline="true" class="search-form-inline" ref="searchForm" :label-width="lableWidth ? lableWidth + 'px' : ''">
      <slot name="condition"></slot>
    </el-form>
    <div class="search-btns">
      <el-button @click="submit" v-show="needSubmit" size="small" type="primary" icon="el-icon-search">
        查询
      </el-button>
      <el-button @click="reset" class="search-form-btn" v-show="needReset" size="small" icon="el-icon-refresh-left">
        重置
      </el-button>
    </div>
  </div>
</template>
<script>
import { Button, Form } from "element-ui";

export default {
  name: "search-group",
  components: {
    "el-button": Button,
    "el-form": Form,
  },
  data() {
    return {
      formItemMarginRight: 16, // 每一项之间的间隔 单位: px
    };
  },
  props: {
    needSubmit: {
      type: Boolean,
      default: true,
    },
    needReset: {
      type: Boolean,
      default: false,
    },
    isGrid: { // 是否是对齐的布局
      type: Boolean,
      default: true,
    },
    lableWidth: {
      default: 100, // 默认文字说明的宽度
    },
  },
  methods: {
    submit() {
      this.$emit("submit");
    },
    reset() {
      this.$emit("reset");
    },
    calcSeachItemLayout() {
      if (this.isGrid) {
        let itemNum // 单行的个数
        if (window.innerWidth < 1600) {
          // 3列
          itemNum = 3
        } else {
          // 4列
          itemNum = 4
        }
        let viewWidth = this.$refs.searchForm.$el.offsetWidth // 查询栏的总宽度
        let itemWidth = viewWidth / itemNum - this.lableWidth - this.formItemMarginRight // 每一个查询项的内容宽度
        let parentNode = this.$slots.condition[0].elm;
        // 给每项设置宽度及间隔
        for (let i = 0; i < parentNode.children.length; i++) {
          let el = parentNode.children[i];
          el.style.marginRight = this.formItemMarginRight + "px";
          let contentDom = el.querySelector(".el-form-item__content");
          if (el.attributes.isLong) {
            // 如果该项有 isLong 属性, 则设为2倍宽度
            contentDom.style.width =
              Number(itemWidth) * 2 +
              Number(this.lableWidth) +
              Number(this.formItemMarginRight) +
              "px";
          } else {
            contentDom.style.width = itemWidth + "px";
          }
        }
      }
    }
  },
  mounted() {
    this.calcSeachItemLayout()
    window.addEventListener("resize", this.calcSeachItemLayout);
  },
  destroyed() {
    window.removeEventListener("resize", this.calcSeachItemLayout);
  },
  watch: {},
};
</script>
