<template lang="pug">
    div(id="animation-loading-gcc" v-if="isShow" :style="maskColor ? `background: ${maskColor}` : ''").animation-content
      div(v-if="shapeType === 'square'").square-shape
        div(v-for="item in 4" :style="pointColor ? `background: ${pointColor}` : ''").point
      div(v-if="shapeType === 'circle'").circle-shape
        div(v-for="item in 4" :style="pointColor ? `background: ${pointColor}` : ''").point
      div(v-if="shapeType === 'rotateSquare'").rotate-square-shape
        div.rotate-square-block
          div(v-for="item in 2" :style="pointColor ? `background: ${pointColor}` : ''").point2
          br
          div(v-for="item in 2" :style="pointColor ? `background: ${pointColor}` : ''").point2
</template>

<script>
export default {
  name: "Animation",
  props: {
    pointColor: {
      type: String,
      default: '',
    },
    maskColor: {
      type: String,
      default: '',
    },
    shapeType: {
      type: String,
      default: 'square',
    },
    appendNode: {
      type: String,
      default: 'body',
    },
    isShow: {
      type: [Boolean, String],
      default: false,
    },
  },
  computed: {
  },
  methods: {
    initSquare() {
      // 按时间轴运动一个对象。//timeline类似Tween集合的容器,默认配置可以放这个容器里
      let tl = gsap.timeline({repeat: -1, yoyo: true,});
      tl.to('.point', {
        duration: 0.8, // 继续时间
        scale: (index, target, targets) => { // 不同下标不同缩放
          return 0.1 * (index+1);
        },
        ease: "power1.inOut",
        stagger: { // 不同格子交错效果
          amount: 0.3,
          grid: 'auto', // 交错内容是网格形状，可以几行几列。测试实际效果和设置'auto'一样
          axis: 'y',
          ease: 'none',
          from: 'start'
        }
      });
      gsap.to(".rotate-square-block", {
        rotation: 360,
        duration: 1.5,
      });
    },
  },
  mounted() {
    this.initSquare();
    // querySelector返回符合要求的第一个对象
    let topDom = document.querySelector('#animation-loading-gcc');
    let bodyDom = document.querySelector(this.appendNode);
    if (this.isShow && bodyDom && topDom && bodyDom.nodeType === 1 && topDom.nodeType === 1) {
      bodyDom.appendChild(topDom);
    }
  }
}
</script>

<style scoped lang="scss">
.animation-content {
  position: absolute;
  top: 0;
  z-index: 9999;
  height: 100%;
  width: 100%;
  background: rgba(10,10,10,0.1);
  .square-shape {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    .point {
      margin: 0 4px;
      background: rgba(32, 82, 149,0.9);
      width: 1vw;
      height: 1vw;
    }
  }
  .circle-shape {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    .point {
      margin: 0 4px;
      background: rgba(32, 82, 149,0.9);
      width: 1vw;
      height: 1vw;
      border-radius: 0.5vw;
    }
  }
  .rotate-square-shape {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    .point2 {
      display: inline-block;
      margin: 0px 1px;
      background: rgba(32, 82, 149,0.9);
      width: 1.5vw;
      height: 1.5vw;
      border-radius: 0.2vw;
    }
  }
}
</style>
