<template>
  <div class="rolling_dynamic">
    <p class="rolling_dynamic_title">
      <slot name="title"></slot>
    </p>
    <div class="rolling_dynamic_cont" :style="`height:${contHeight}px;`">
      <div
        class="rolling_dynamic_cont_list"
        ref="rolling_dynamic_cont_list"
        :class="{ rolling_dynamic_anmi: isAnimate == true }"
      >
        <div v-for="(item, index) in items" :key="index">
          <slot name="list" :data="item"></slot>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "rolling-dynamic",
  props: {
    data: {
      type: Array,
      default: () => {
        return [];
      },
    },
    showNum: {
      type: Number,
      default: 6,
    },
    cacheLength: {
      type: Number,
      default: 15,
    },
    isScroll: {
      type: Boolean,
      default: true,
    }
  },
  data() {
    return {
      isAnimate: false,
      timer: undefined, // 定时器
      items: [],
      contHeight: 0,
      childHeight: 0,
    };
  },
  mounted() {
    this.items = [...this.data];
    if (this.items.length > this.showNum && this.items.length <= this.cacheLength) {
      this.items.push(this.items[0]);
    }
    this.$nextTick(() => {
      this.childHeight =
        this.$refs.rolling_dynamic_cont_list.querySelector(
          "div:nth-child(1)"
        ).offsetHeight;
      this.contHeight = this.childHeight * this.showNum;
      this.timer = setInterval(this.scroll, 2000);
    });
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  watch: {
    data(newVal) {
      this.items = this.items.concat(newVal);
    },
  },
  computed: {},
  methods: {
    scroll() {
      if (!this.isScroll || this.items.length <= this.showNum) {
        return;
      }
      const element = this.$refs.rolling_dynamic_cont_list;
      element.style.marginTop = `-${this.childHeight}px`;
      this.isAnimate = !this.isAnimate;
      setTimeout(() => {
        this.items.shift();
        if (this.items.length < this.cacheLength) {
          this.items.push(this.items[0]);
        }
        element.style.marginTop = "0px";
        // 取消transition样式，否则出现回弹问题
        this.isAnimate = !this.isAnimate;
      }, 500);
    },
  },
};
</script>

<style lang="scss" rel="style/scss" scoped>
.rolling_dynamic {
  ::-webkit-scrollbar {
    width: 0;
    height: 0;
  }
  .rolling_dynamic_cont {
    overflow: hidden;
    transition: all 0.5s;
    .rolling_dynamic_anmi {
      transition: all 0.5s;
    }
  }
}
</style>
