<template lang="pug">
  div.container
    div(id="demo" :option="optionTemp" :style="`width: ${width};`")
</template>

<script>
import HighCharts from "highcharts";
import highcharts3d from "highcharts/highcharts-3d";
// 因为要使用3d效果，所以这段代码必须加上，不然跑出来的效果就是2d效果
highcharts3d(HighCharts);
/*
* 旧版中文文档：https://api.highcharts.com.cn/legacy#chart.renderTo
* 官网最新英文文档：https://api.highcharts.com/highcharts/tooltip.stickOnContact
* 简单中文入门教程：https://wizardforcel.gitbooks.io/highcharts-tutorial/content/3.html
* 引入步骤：
* 1、yarn add highcharts
* 2、lib文件夹中新建个highchart文件夹，新建js见src/lib/highchart/index.js
* 3、在main.js中引用，见src/main.js
* */
export default {
  name: "HighPie",
  props: {
    option: {
      default: () => {},
    },
    width: {
      default: '600px',
    },
    rotateTime: { // 转动时间，data需要带有sliced和selected
      default: 3600000,
    },
  },
  data() {
    return {
      // 避免直接修改props
      optionTemp: this.option,
      rotateNum: 0,
      rotateTimer: null,
      chart: null,
    };
  },
  methods: {
    loadChart() {
      // console.log(this.rotateNum);
      this.chart = HighCharts.chart("demo", this.optionTemp);
    },
    startRotate() {
      this.rotateTimer = setInterval(this.rotate, 1500);
    },
    // 通过修改startAngle进行旋转,selected突出选中块
    rotate() {
      // this.rotateNum = this.rotateNum + 30;
      // if (this.rotateNum == 270) {
      //   this.rotateNum = -90;
      // }
      // this.optionTemp.series[0].startAngle = this.rotateNum;

      let seriesData = this.optionTemp.series[0].data;
      for (let i = 0; i < seriesData.length ; i++) {
        if (seriesData[i].selected && seriesData[i].selected == true) {
          seriesData[i].sliced = false;
          seriesData[i].selected = false;
          if (i == seriesData.length - 1) {
            seriesData[0].sliced = true;
            seriesData[0].selected = true;
            this.loadChart();
            return;
          } else {
            seriesData[i+1].sliced = true;
            seriesData[i+1].selected = true;
            this.loadChart();
            return
          }
        }
      }
    },
    stopRotate() {
      clearInterval(this.rotateTimer);
      this.rotateTimer = null;
    }
  },
  mounted() {
    let defaultOption = {
      chart: {
        type: 'pie', // 图类型
        marginRight: 180, // 设置右边距给右图例留空间
        backgroundColor: 'rgba(0, 51, 116, 0)', // 背景颜色
        options3d: { // 开启3D
          enabled: true,
          alpha: 60,
          beta: 0
        }
      },
      credits: {
        enabled: false, // 禁用版权信息
      },
      title: {
        text: ''
      },
      tooltip: {
        enabled: true,
        shared: false,
        pointFormat: '{series.name}: <b>{point.percentage:.1f}%</b>' // 悬浮窗自定义内容
      },
      plotOptions: {
        pie: {
          allowPointSelect: true, // 支持鼠标点击
          cursor: 'pointer', // 鼠标移动出现'手'形状
          depth: 30, // 3d饼图的厚度
          size: '90%', // 饼图大小比例
          innerSize: 160, // 中间挖空饼图大小，增加则为环形
          dataLabels: { // 饼图标签
            enabled: false, // 启用/关闭数据标签
            format: '{point.name}' // 标签数据
          }
        },
        series: {
          animation: false, // 进场的缩放动画
        }
      },
      legend: { // 图例配置
        layout: 'vertical', //设置垂直图例
        labelFormatter: function () { // 自定义图例内容
          return this.name + ' ' + `${this.percentage.toFixed(1)}` + '%';
        },
        symbolHeight: 12, // 标志大小
        symbolRadius: 6, // 标志圆角，默认是大小的一半，即圆
        borderRadius: 4, // 图例边框圆角
        backgroundColor: 'rgba(0, 51, 116, 1)', // 图例背景色
        itemStyle: { // 图例样式
          color: 'rgba(255, 255, 255, 0.9)',
          'letter-spacing': 1,
        },
        itemHoverStyle: { // 图例鼠标悬浮样式
          color: 'rgba(255, 255, 255, 1)'
        },
        align: 'right', // 水平位置
        verticalAlign: 'middle', // 垂直对齐位置
        floating: true, // 设置浮动位置
        x: -25,
        y: 0
      },
      series: [{
        type: 'pie',
        name: 'demo', // 鼠标悬浮显示的饼图名字
        startAngle: 0,
        showInLegend: true, // 显示图例
        data: [
          {
            name: 'demo1', // tooltip文字
            y: 22, // 饼图数据
            sliced: true, // 选中块增加偏移，提现立体感
            selected: true, // 默认选中此数据
            color: 'rgba(255,89,89, 0.75)' // 模块颜色
          }, {
            name: 'demo2', // tooltip文字
            y: 12, // 饼图数据
            sliced: true, // 选中块增加偏移，提现立体感
            selected: true, // 默认选中此数据
            color: 'rgba(88,208,89,0.75)' // 模块颜色
          }, {
            name: 'demo3', // tooltip文字
            y: 32, // 饼图数据
            sliced: true, // 选中块增加偏移，提现立体感
            selected: true, // 默认选中此数据
            color: 'rgba(147,219,255,0.75)' // 模块颜色
          }, {
            name: 'demo4', // tooltip文字
            y: 29, // 饼图数据
            sliced: true, // 选中块增加偏移，提现立体感
            selected: true, // 默认选中此数据
            color: 'rgba(255,207,95,0.75)' // 模块颜色
          },
        ]
      }]
    };
    if (!this.optionTemp || Object.keys(this.optionTemp).length === 0) {
      this.optionTemp = defaultOption;
    } else {
      this.optionTemp = {...defaultOption, ...this.optionTemp}
    }
    this.loadChart();
    // this.chart.tooltip.refresh([this.chart.series[0].data[0],this.chart.series[1].data[0]]);
    this.startRotate();
  },
  destroyed() {
    this.stopRotate();
  }
}
</script>

