<template>
  <div class="search-panel">
    <el-form :inline="true" class="search-form-inline">
      <slot name="condition"></slot>
      <slot name="moreCondition"></slot>
      <el-collapse-transition>
        <div
          class="condition-group"
          ref="condition2"
          v-show="thisHasMoreCondition && thisShowMoreCondition"
        />
      </el-collapse-transition>
      <div class="search-btn-group">
        <el-button
          @click="toggleMoreCondition"
          class="search-form-btn"
          size="small"
          v-if="thisHasMoreCondition"
        >
          {{!thisShowMoreCondition ? '更多' : '收起'}}
        </el-button>
        <el-button
          @click="submit"
          class="search-form-btn"
          v-show="needSubmit"
          size="small"
          type="primary"
          icon="el-icon-search"
        >
          查询
        </el-button>
        <el-button
          @click="reset"
          class="search-form-btn"
          v-show="needReset"
          size="small"
          icon="el-icon-refresh-left"
        >
          重置
        </el-button>
      </div>
    </el-form>
  </div>
</template>
<script>
import CollapseTransition from 'element-ui/lib/transitions/collapse-transition';
import { Button, ButtonGroup, Form } from 'element-ui';

export default {
  name: 'table-search',
  data() {
    return {
      thisShowMoreCondition: false,
      thisHasMoreCondition: false,
    };
  },
  props: {
    showMoreCondition: {
      type: Boolean,
      default: false,
    },
    hasMoreCondition: {
      type: Boolean,
      default: false,
    },
    needSubmit: {
      type: Boolean,
      default: true,
    },
    needReset: {
      type: Boolean,
      default: false,
    },
  },
  components: {
    'el-button': Button,
    'el-button-group': ButtonGroup,
    'el-form': Form,
    'el-collapse-transition': CollapseTransition
  },
  methods: {
    submit () {
      this.$emit('submit');
    },
    reset () {
      this.$emit('reset');
    },
    toggleMoreCondition () {
      this.thisShowMoreCondition = !this.thisShowMoreCondition;
    }
  },
  mounted () {
    this.thisShowMoreCondition = this.showMoreCondition;
    this.thisHasMoreCondition = this.hasMoreCondition;
    let conditionCount = this.$slots.condition[0].elm.childElementCount;
    let moreConditionCount = this.$slots.moreCondition ? this.$slots.moreCondition[0].elm.childElementCount : 0;
    if (conditionCount > 3 || moreConditionCount > 0) {
      this.thisHasMoreCondition = true;
    }
    if (moreConditionCount > 0) {
      let parentNode = this.$slots.moreCondition[0].elm;
      for (let i = moreConditionCount - 1; i > -1; i--) {
        let el = parentNode.children[i];
        let home = document.createComment('');
        parentNode.replaceChild(home, el);
        this.$refs.condition2.appendChild(el);
      }
    }
    if (conditionCount > 3) {
      let parentNode = this.$slots.condition[0].elm;
      for (let i = conditionCount - 1; i > 2; i--) {
        let el = parentNode.children[i];
        let home = document.createComment('');
        parentNode.replaceChild(home, el);
        this.$refs.condition2.prepend(el);
      }
    }
  },
  watch: {
  }
};
</script>
