.search-panel {
  background-color: #fff;
  padding: 8px 8px;
  border-bottom: 1px solid #DCE4E6;
}
.search-form-inline {
  line-height: 34px;
  position: relative;
  .condition-group {
    width: 100%;
    margin-top: 2px;
  }
  .el-form-item {
    margin-bottom: 0;
    line-height: 34px;
    height: 34px;
    margin-right:16px;
    &.el-form-item--small {
      .el-form-item__label,
      .el-form-item__content {
        height: 34px;
        line-height: 34px;
      }
    }
  }
  .el-date-editor{
    .el-range-separator {
      padding: 0;
    }
  }
}
.search-btn-group {
  display: inline-block;
  position: absolute;
  top: 2px;
  right: 0;
  .search-form-btn {
    margin-left: 12px;
  }
}
</style>
